//
// Responsive: Utility classes
// --------------------------------------------------


// IE10 in Windows (Phone) 8
//
// Support for responsive views via media queries is kind of borked in IE10, for
// Surface/desktop in split view and for Windows Phone 8. This particular fix
// must be accompanied by a snippet of JavaScript to sniff the user agent and
// apply some conditional CSS to *only* the Surface/desktop Windows 8. Look at
// our Getting Started page for more information on this bug.
//
// For more information, see the following:
//
// Issue: https://github.com/twbs/bootstrap/issues/10497
// Docs: http://getbootstrap.com/getting-started/#support-ie10-width
// Source: http://timkadlec.com/2013/01/windows-phone-8-and-device-width/
// Source: http://timkadlec.com/2012/10/ie10-snap-mode-and-responsive-design/

@-ms-viewport {
  width: device-width;
}


// Visibility utilities
// Note: Deprecated .visible-xs, .visible-sm, .visible-md, and .visible-lg as of v3.2.0
.visible-xs,
.visible-sm,
.visible-md,
.visible-lg {
  .responsive-invisibility();
}

.visible-xs-block,
.visible-xs-inline,
.visible-xs-inline-block,
.visible-sm-block,
.visible-sm-inline,
.visible-sm-inline-block,
.visible-md-block,
.visible-md-inline,
.visible-md-inline-block,
.visible-lg-block,
.visible-lg-inline,
.visible-lg-inline-block {
  display: none !important;
}

.visible-xs {
  @media (max-width: @screen-xs-max) {
    .responsive-visibility();
  }
}
.visible-xs-block {
  @media (max-width: @screen-xs-max) {
    display: block !important;
  }
}
.visible-xs-inline {
  @media (max-width: @screen-xs-max) {
    display: inline !important;
  }
}
.visible-xs-inline-block {
  @media (max-width: @screen-xs-max) {
    display: inline-block !important;
  }
}

.visible-sm {
  @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
    .responsive-visibility();
  }
}
.visible-sm-block {
  @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
    display: block !important;
  }
}
.visible-sm-inline {
  @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
    display: inline !important;
  }
}
.visible-sm-inline-block {
  @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
    display: inline-block !important;
  }
}

.visible-md {
  @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
    .responsive-visibility();
  }
}
.visible-md-block {
  @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
    display: block !important;
  }
}
.visible-md-inline {
  @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
    display: inline !important;
  }
}
.visible-md-inline-block {
  @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
    display: inline-block !important;
  }
}

.visible-lg {
  @media (min-width: @screen-lg-min) {
    .responsive-visibility();
  }
}
.visible-lg-block {
  @media (min-width: @screen-lg-min) {
    display: block !important;
  }
}
.visible-lg-inline {
  @media (min-width: @screen-lg-min) {
    display: inline !important;
  }
}
.visible-lg-inline-block {
  @media (min-width: @screen-lg-min) {
    display: inline-block !important;
  }
}

.hidden-xs {
  @media (max-width: @screen-xs-max) {
    .responsive-invisibility();
  }
}
.hidden-sm {
  @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
    .responsive-invisibility();
  }
}
.hidden-md {
  @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
    .responsive-invisibility();
  }
}
.hidden-lg {
  @media (min-width: @screen-lg-min) {
    .responsive-invisibility();
  }
}

// primaERP .forced-* class ancestor combinations added ------- START

.set-forced-visible-to-invisible(xs);
.set-forced-visible-to-invisible(sm);
.set-forced-visible-to-invisible(md);
.set-forced-visible-to-invisible(lg);
.set-forced-visible-to-visible(xs);
.set-forced-visible-to-visible(sm);
.set-forced-visible-to-visible(md);
.set-forced-visible-to-visible(lg);
.set-forced-hidden-to-visible(xs);
.set-forced-hidden-to-visible(sm);
.set-forced-hidden-to-visible(md);
.set-forced-hidden-to-visible(lg);
.set-forced-hidden-to-invisible(xs);
.set-forced-hidden-to-invisible(sm);
.set-forced-hidden-to-invisible(md);
.set-forced-hidden-to-invisible(lg);

.set-forced-visible-to-invisible(@bpoint) {
  // VISIBLE-*
	.forced-@{bpoint} .visible-xs when not(@bpoint=xs) {
    .responsive-invisibility();
  }
  .forced-@{bpoint} .visible-sm when not(@bpoint=sm) {
    .responsive-invisibility();
  }
  .forced-@{bpoint} .visible-md when not(@bpoint=md) {
    .responsive-invisibility();
  }
  .forced-@{bpoint} .visible-lg when not(@bpoint=lg) {
    .responsive-invisibility();
  }
  // VISIBLE-* cascade 2nd level
	.forced-@{bpoint} .forced-xs .visible-@{bpoint} when not(@bpoint=xs) {
    .responsive-invisibility();
  }
  .forced-@{bpoint} .forced-sm .visible-@{bpoint} when not(@bpoint=sm) {
    .responsive-invisibility();
  }
  .forced-@{bpoint} .forced-md .visible-@{bpoint} when not(@bpoint=md) {
    .responsive-invisibility();
  }
  .forced-@{bpoint} .forced-lg .visible-@{bpoint} when not(@bpoint=lg) {
    .responsive-invisibility();
  }
}

.set-forced-visible-to-visible(@bpoint) {
  // VISIBLE-*
  .forced-@{bpoint} .visible-xs when (@bpoint=xs) {
    .responsive-visibility();
  }
  .forced-@{bpoint} .visible-sm when (@bpoint=sm) {
    .responsive-visibility();
  }
  .forced-@{bpoint} .visible-md when (@bpoint=md) {
    .responsive-visibility();
  }
  .forced-@{bpoint} .visible-lg when (@bpoint=lg) {
    .responsive-visibility();
  }
  // VISIBLE-* cascade 2nd level
  .forced-@{bpoint} .forced-xs .visible-xs when not(@bpoint=xs) {
    .responsive-visibility();
  }
  .forced-@{bpoint} .forced-sm .visible-sm when not(@bpoint=sm) {
    .responsive-visibility();
  }
  .forced-@{bpoint} .forced-md .visible-md when not(@bpoint=md) {
    .responsive-visibility();
  }
  .forced-@{bpoint} .forced-lg .visible-lg when not(@bpoint=lg) {
    .responsive-visibility();
  }
}

.set-forced-hidden-to-visible(@bpoint) {
  // HIDDEN-*
  .forced-@{bpoint} .hidden-xs when not(@bpoint=xs) {
    .responsive-visibility();
  }
  .forced-@{bpoint} .hidden-sm when not(@bpoint=sm) {
    .responsive-visibility();
  }
  .forced-@{bpoint} .hidden-md when not(@bpoint=md) {
    .responsive-visibility();
  }
  .forced-@{bpoint} .hidden-lg when not(@bpoint=lg) {
    .responsive-visibility();
  }
  // HIDDEN-* cascade 2nd level
  .forced-@{bpoint} .forced-xs .hidden-@{bpoint} when not(@bpoint=xs) {
    .responsive-visibility();
  }
  .forced-@{bpoint} .forced-sm .hidden-@{bpoint} when not(@bpoint=sm) {
    .responsive-visibility();
  }
  .forced-@{bpoint} .forced-md .hidden-@{bpoint} when not(@bpoint=md) {
    .responsive-visibility();
  }
  .forced-@{bpoint} .forced-lg .hidden-@{bpoint} when not(@bpoint=lg) {
    .responsive-visibility();
  }
}	

.set-forced-hidden-to-invisible(@bpoint) {	
  // HIDDEN-*
	.forced-@{bpoint} .hidden-xs when (@bpoint=xs) {
    .responsive-invisibility();
  }
  .forced-@{bpoint} .hidden-sm when (@bpoint=sm) {
    .responsive-invisibility();
  }
  .forced-@{bpoint} .hidden-md when (@bpoint=md) {
    .responsive-invisibility();
  }
  .forced-@{bpoint} .hidden-lg when (@bpoint=lg) {
    .responsive-invisibility();
  }
  // HIDDEN-* cascade 2nd level
  .forced-@{bpoint} .forced-xs .hidden-xs when not(@bpoint=xs) {
    .responsive-invisibility();
  }
  .forced-@{bpoint} .forced-sm .hidden-sm when not(@bpoint=sm) {
    .responsive-invisibility();
  }
  .forced-@{bpoint} .forced-md .hidden-md when not(@bpoint=md) {
    .responsive-invisibility();
  }
  .forced-@{bpoint} .forced-lg .hidden-lg when not(@bpoint=lg) {
    .responsive-invisibility();
  }
}

// primaERP .forced-* class ancestor combinations added ------- END


// Print utilities
//
// Media queries are placed on the inside to be mixin-friendly.

// Note: Deprecated .visible-print as of v3.2.0
.visible-print {
  .responsive-invisibility();

  @media print {
    .responsive-visibility();
  }
}
.visible-print-block {
  display: none !important;

  @media print {
    display: block !important;
  }
}
.visible-print-inline {
  display: none !important;

  @media print {
    display: inline !important;
  }
}
.visible-print-inline-block {
  display: none !important;

  @media print {
    display: inline-block !important;
  }
}

.hidden-print {
  @media print {
    .responsive-invisibility();
  }
}
